<template>
  <div>
    <!---->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!---->
    <el-card>
      <!---->
      <el-row :gutter="20">
        <el-col :span="8">
          <el-input placeholder="" v-model="queryInfo.query" clearable @clear="getUserList">
            <el-button slot="append" icon="el-icon-search" @click="getUserList"></el-button>
          </el-input>
        </el-col>
        <el-col :span="4">
          <el-button type="primary" @click="addDialogVisible = true"></el-button>
        </el-col>
      </el-row>
      <!---->
      <el-table
        :data="userlist"
        border
        stripe>
        <el-table-column type="index">
        </el-table-column>
        <el-table-column prop="username" label="">
        </el-table-column>
        <el-table-column prop="roleName" label="">
        </el-table-column>
        <el-table-column prop="addTime" label="">
          <template slot-scope="scope">
            {{scope.row.addTime}}
          </template>
        </el-table-column>
        <el-table-column label="" width="300">
          <template slot-scope="scope">
            <el-tooltip effect="dark" content="" placement="top" :enterable="false">
              <el-button type="primary" icon="el-icon-edit" size="mini" @click="showEditDialog(scope.row.userId)"></el-button>
            </el-tooltip>
            <el-tooltip effect="dark" content="" placement="top" :enterable="false">
              <el-button type="danger" icon="el-icon-delete" size="mini" @click="removeUserById(scope.row.userId)"></el-button>
            </el-tooltip>
            <el-tooltip effect="dark" content="" placement="top" :enterable="false">
              <el-button type="warning" icon="el-icon-setting" size="mini" @click="setRole(scope.row)"></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>
      <!---->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[5, 10, 15, 20]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
        background>
      </el-pagination>
    </el-card>
    <!---->
    <el-dialog
      title=""
      :visible.sync="addDialogVisible"
      width="50%"
      @close="addDialogClosed">
      <!---->
      <el-form :model="addForm" :rules="addFormRules" ref="addFormRef" label-width="70px">
        <el-form-item label="" prop="username">
          <el-input v-model="addForm.username"></el-input>
        </el-form-item>
        <el-form-item label="" prop="password">
          <el-input v-model="addForm.password"></el-input>
        </el-form-item>
      </el-form>
      <!---->
      <span slot="footer" class="dialog-footer">
        <el-button @click="addDialogVisible = false"> </el-button>
        <el-button type="primary" @click="addUser"> </el-button>
      </span>
    </el-dialog>
    <!---->
    <el-dialog
      title=""
      :visible.sync="editDialogVisible"
      width="50%"
      @close="editDialogClosed">
      <el-form :model="editForm" :rules="editFormRules" ref="editFormRef" label-width="70px">
        <el-form-item label="">
          <el-input v-model="editForm.username" disabled></el-input>
        </el-form-item>
        <el-form-item label="" prop="password">
          <el-input v-model="editForm.password"></el-input>
        </el-form-item>
      </el-form>
      <!---->
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false"> </el-button>
        <el-button type="primary" @click="editUserInfo"> </el-button>
      </span>
    </el-dialog>
    <!---->
    <el-dialog
      title=""
      :visible.sync="setRoleDialogVisible"
      width="50%"
      @close="setRoleDialogClosed">
      <div>
        <p>: {{userInfo.username}}</p>
        <p>: {{userInfo.roleName}}</p>
        <p>
          
          <el-select v-model="selectedRoleId" placeholder="">
            <el-option
              v-for="item in rolesList"
              :key="item.id"
              :label="item.roleName"
              :value="item.id">
            </el-option>
          </el-select>
        </p>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="setRoleDialogVisible = false"> </el-button>
        <el-button type="primary" @click="saveRoleInfo"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data () {
    return {
      queryInfo: {
        query: '',
        pagenum: 1,
        pagesize: 10
      },
      userlist: [
        {
          userId: 9,
          username: 'admin',
          roleName: '',
          addTime: 1582192840
        },
        {
          userId: 9,
          username: 'teacher',
          roleName: '',
          addTime: 1582192840
        }
      ],
      total: 0,
      addDialogVisible: false,
      addForm: {
        username: '',
        password: ''
      },
      addFormRules: {
        username: [
          { required: true, message: '', trigger: 'blur' },
          { min: 3, max: 10, message: ' 3  10 ', trigger: 'blur' }
        ],
        password: [
          { required: true, message: '', trigger: 'blur' },
          { min: 6, max: 15, message: ' 6  15 ', trigger: 'blur' }
        ]
      },
      // 
      editDialogVisible: false,
      editForm: {
      },
      // 
      editFormRules: {
        password: [
          { required: true, message: '', trigger: 'blur' },
          { min: 6, max: 15, message: ' 6  15 ', trigger: 'blur' }
        ]
      },
      // 
      setRoleDialogVisible: false,
      // 
      userInfo: {},
      // 
      rolesList: [
      ],
      // id
      selectedRoleId: ''
    }
  },
  created () {
    this.getUserList()
  },
  methods: {
    async getUserList () {
      const { data: res } = await this.$http.get('/admin/user', {
        params: this.queryInfo
      })
      if (res.data.meta.status !== '200') return this.$message.error('')
      this.userlist = res.data.userList
      this.total = res.data.total
    },
    //  pagesize 
    handleSizeChange (newSize) {
      // console.log(newSize)
      this.queryInfo.pagesize = newSize
      this.getUserList()
    },
    // 
    handleCurrentChange (newPage) {
      // console.log(newPage)
      this.queryInfo.pagenum = newPage
      this.getUserList()
    },
    // 
    addDialogClosed () {
      this.$refs.addFormRef.resetFields()
    },
    // 
    addUser () {
      this.$refs.addFormRef.validate(async valid => {
        if (!valid) return null
        // 
        const { data: res } = await this.$http.put('/admin/user', this.addForm)
        if (res.data.meta.status !== '200') {
          this.$message.error('')
        }
        this.$message.success('')
        // 
        this.addDialogVisible = false
        // 
        this.getUserList()
      })
    },
    // 
    async showEditDialog (id) {
      const { data: res } = await this.$http.get('/admin/user/' + id + '/password')
      if (res.data.meta.status !== '200') {
        return this.$message.error('')
      }
      this.editForm = {
        username: res.data.username,
        password: res.data.password
      }
      this.editDialogVisible = true
    },
    // 
    editDialogClosed () {
      this.editForm = {}
    },
    // 
    editUserInfo () {
      this.$refs.editFormRef.validate(async valid => {
        if (!valid) return null
        // 
        const { data: res } = await this.$http.put('/admin/user/' + this.editForm.id, {
          email: this.editForm.email,
          mobile: this.editForm.mobile
        })
        if (res.data.meta.status !== '200') {
          return this.$message.error('')
        }
        // 
        this.editDialogVisible = false
        // 
        this.getUserList()
        // 
        this.$message.success('')
      })
    },
    // Id
    async removeUserById (id) {
      // 
      const confirmResult = await this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }
      ).catch(err => err)
      //  'confirm'
      //  cancel
      if (confirmResult !== 'confirm') {
        return this.$message.info('')
      }
      const { data: res } = await this.$http.delete('/admin/user/' + id)
      if (res.data.meta.status !== '200') {
        return this.$message.error('')
      }
      this.$message.success('')
      this.getUserList()
    },
    // 
    async setRole (userInfo) {
      this.userInfo = userInfo
      // // 
      const { data: res } = await this.$http.get('/admin/roles')
      if (res.data.meta.status !== '200') {
        return this.$message.error('')
      }
      this.rolesList = res.data.rolesList
      this.setRoleDialogVisible = true
    },
    // 
    async saveRoleInfo () {
      if (!this.selectedRoleId && this.selectedRoleId !== 0) {
        return this.$message.error('')
      }
      const { data: res } = await this.$http.put(`/admin/user/${this.userInfo.userId}/role`, {
        rid: this.selectedRoleId
      })
      if (res.data.meta.status !== '200') {
        return this.$message.error('')
      }
      this.$message.success('')
      this.getUserList()
      this.setRoleDialogVisible = false
    },
    // 
    setRoleDialogClosed () {
      this.selectedRoleId = ''
      this.userInfo = {}
    }
  }
}
</script>

<style lang="less" scoped>

</style>
