<template>
  <div>
    <!---->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
      <!-- 2.EChartsDom -->
      <div id="main"></div>
    </el-card>
  </div>
</template>

<script>
// 1. echarts
import echarts from 'echarts'

export default {
  data () {
    return {
      option: {
        title: {
          text: '',
          subtext: ' ',
          left: 'center'
        },
        tooltip: {
          trigger: 'item',
          formatter: '{a} <br/>{b} : {c} ({d}%)'
        },
        legend: {
          orient: 'vertical',
          left: 'left',
          data: ['', '', '']
        },
        series: [
          {
            name: '',
            type: 'pie',
            radius: '55%',
            center: ['50%', '60%'],
            data: [
              {
                value: 0,
                name: '',
                itemStyle: {
                  color: '#26A65B'
                }
              },
              {
                value: 0,
                name: '',
                itemStyle: {
                  color: '#D24D57'
                }
              },
              {
                value: 0,
                name: '',
                itemStyle: {
                  color: '#EB7347'
                }
              }
            ],
            emphasis: {
              itemStyle: {
                shadowBlur: 10,
                shadowOffsetX: 0,
                shadowColor: 'rgba(0, 0, 0, 0.5)'
              }
            }
          }
        ]
      }
    }
  },
  created () {
  },
  // 
  async mounted () {
    // 3.domecharts
    var myChart = echarts.init(document.getElementById('main'))
    const { data: res1 } = await this.$http.get('/admin/report')
    if (res1.data.meta.status !== '200') {
      this.$message.error('')
    }

    var res = res1.data.reportData
    this.option.series[0].data.forEach((item, index) => { item.value = res[index] })
    this.option.title.subtext = this.option.title.subtext + this.sum(res1.data.reportData) + ' '
    // 5.
    myChart.setOption(this.option)
  },
  methods: {
    sum(arr) {
      var s = 0
      for (var i = arr.length - 1; i >= 0; i--) {
        s += arr[i]
      }
      return s
    }
  }
}
</script>

<style lang="less" scoped>
@media only screen and (min-width: 1024px) {
  #main {
  width: 750px;
  height: 400px;
  margin-left: 150px;
  }
}

@media only screen and (max-width: 1024px) {
  #main {
  width: 240px;
  height: 500px;
  /*margin-left: 150px;*/
  }
}
</style>
