<template>
  <div>
    <!---->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!---->
    <el-card>
      <!---->
      <el-row :gutter="20">
        <el-col :span="8">
          <el-input placeholder="" v-model="queryInfo.query" clearable @clear="getParticipantsList(1)">
            <el-button slot="append" icon="el-icon-search" @click="getParticipantsList(1)"></el-button>
          </el-input>
        </el-col>
        <el-col :span="8">
          <el-switch
            class="isnew"
            v-model="value"
            active-color="#13ce66"
            inactive-color="#ff4949"
            active-text=""
            inactive-text=""
            @change="participantsChange">
          </el-switch>
        </el-col>
      </el-row>
      <!---->
      <el-table
        :data="userlist"
        border
        stripe>
        <el-table-column prop="stuId" label="" width="70">
        </el-table-column>
        <el-table-column prop="times" label="">
        </el-table-column>
        <el-table-column prop="name" label="">
        </el-table-column>
        <el-table-column prop="stuNum" label="">
        </el-table-column>
        <el-table-column prop="college" label="">
        </el-table-column>
        <!-- <el-table-column prop="star" label="">
        </el-table-column> -->
        <el-table-column prop="theWay" label="">
        </el-table-column>
        <el-table-column label="" prop="status">
          <template slot-scope="scope">
            <el-tag type="warning" v-if="scope.row.status === '0'"></el-tag>
            <el-tag type="success" v-else-if="scope.row.status === '1'"></el-tag>
            <el-tag type="danger" v-else></el-tag>
          </template>
        </el-table-column>
        <el-table-column label="" width="300">
        <template slot-scope="scope">
          <el-tooltip content="" placement="top">
          <el-button type="info" icon="el-icon-view" size="mini" @click="setDeclarationInfo(scope.row)"></el-button>
          </el-tooltip>
          <el-tooltip content="" placement="top">
          <el-button type="primary" icon="el-icon-edit" size="mini" @click="setEditInfo(scope.row)"></el-button>
          </el-tooltip>
          <el-tooltip content="" placement="top">
          <el-button type="warning" icon="el-icon-setting" size="mini" @click="setStatus(scope.row)"></el-button>
          </el-tooltip>
        </template>
        </el-table-column>
      </el-table>
      <!---->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[1, 2, 5, 10]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
        background>
      </el-pagination>
    </el-card>
    <!---->
    <el-dialog
      title=""
      :visible.sync="declarationInfoVisible"
      width="70%"
      @close="declarationInfoClosed">
      <el-table :data="declarationInfo">
        <el-table-column property="info" label="" width="150"></el-table-column>
        <el-table-column property="content" label=""></el-table-column>
      </el-table>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="declarationInfoVisible = false"> </el-button>
      </span>
    </el-dialog>
    <!---->
    <el-dialog
      title=""
      :visible.sync="modifyStatusVisible"
      width="50%"
      @close="modifyStatusClosed">
      <div>
        <p>: {{ptpInfo.name}}</p>
        <p v-if="ptpInfo.status === '0'">: </p>
        <p v-else-if="ptpInfo.status === '1'">: </p>
        <p v-else>: </p>
        <p>
          
          <el-select v-model="selectedStatusId" placeholder="" value="">
            <el-option
              v-for="item in statusList"
              :key="item.id"
              :label="item.status"
              :value="item.id">
            </el-option>
          </el-select>
        </p>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="modifyStatusVisible = false"> </el-button>
        <el-button type="primary" @click="handleStatusChange"> </el-button>
      </span>
    </el-dialog>
    <!---->
    <el-dialog
      title=""
      :visible.sync="editDialogVisible"
      width="70%"
      @close="editDialogClosed">
      <el-form :model="editForm" :rules="editFormRules" ref="editFormRef">
        <el-form-item label="">
          <el-input v-model="editForm.name" disabled></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="editForm.stuNum" disabled></el-input>
        </el-form-item>
        <el-form-item label="(//)" prop="person">
          <el-input v-model="editForm.person" type="textarea" maxlength="500" show-word-limit></el-input>
        </el-form-item>
        <el-form-item label="(///)" prop="honor">
          <el-input v-model="editForm.honor" type="textarea" maxlength="500" show-word-limit></el-input>
        </el-form-item>
        <el-form-item label="(/)" prop="rank">
          <el-input v-model="editForm.rank"></el-input>
        </el-form-item>
        <el-form-item label="(////)" prop="paper">
          <el-input v-model="editForm.paper" type="textarea" maxlength="500" show-word-limit></el-input>
        </el-form-item>
        <el-form-item label="(///)" prop="patent">
          <el-input v-model="editForm.patent" type="textarea" maxlength="500" show-word-limit></el-input>
        </el-form-item>
        <el-form-item label="(///)" prop="techprogram">
          <el-input v-model="editForm.techprogram" type="textarea" maxlength="500" show-word-limit></el-input>
        </el-form-item>
        <el-form-item label="" prop="reason">
          <el-input v-model="editForm.reason" type="textarea" maxlength="500" minlength="300" show-word-limit></el-input>
        </el-form-item>
        <el-form-item label="" prop="assistant">
          <el-input v-model="editForm.assistant"></el-input>
        </el-form-item>
        <el-form-item label="" prop="phonenum">
          <el-input v-model="editForm.phonenum"></el-input>
        </el-form-item>
        <!-- <el-form-item label="">
          <el-input v-model="editForm.star"></el-input>
        </el-form-item>
        <el-form-item label="" prop="rank">
          <el-input v-model="editForm.rank"></el-input>
        </el-form-item>
        <el-form-item label="" prop="scholarship">
          <el-input v-model="editForm.scholarship" type="textarea" maxlength="500" show-word-limit></el-input>
        </el-form-item>
        <el-form-item label="" prop="honor">
          <el-input v-model="editForm.honor" type="textarea" maxlength="500" show-word-limit></el-input>
        </el-form-item>
        <el-form-item label="" prop="techHonor">
          <el-input v-model="editForm.techHonor" type="textarea" maxlength="500" show-word-limit></el-input>
        </el-form-item>
        <el-form-item label="" prop="socialPractice">
          <el-input v-model="editForm.socialPractice" type="textarea" maxlength="500" show-word-limit></el-input>
        </el-form-item>
        <el-form-item label="" prop="rankTest">
          <el-input v-model="editForm.rankTest" type="textarea" maxlength="500" show-word-limit></el-input>
        </el-form-item>
        <el-form-item label="" prop="paper">
          <el-input v-model="editForm.paper" type="textarea" maxlength="500" show-word-limit></el-input>
        </el-form-item>
        <el-form-item label="" prop="others">
          <el-input v-model="editForm.others" type="textarea" maxlength="500" show-word-limit></el-input>
        </el-form-item>
        <el-form-item label="" prop="goodDeeds">
          <el-input v-model="editForm.goodDeeds" type="textarea" maxlength="1000" show-word-limit></el-input>
        </el-form-item>
        <el-form-item label="" prop="motto">
          <el-input v-model="editForm.motto" type="textarea" maxlength="40" show-word-limit></el-input>
        </el-form-item>
        <el-form-item label="" prop="assistant">
          <el-input v-model="editForm.assistant"></el-input>
        </el-form-item>
        <el-form-item label="" prop="phoneNum">
          <el-input v-model="editForm.phoneNum"></el-input>
        </el-form-item> -->
      </el-form>
      <!---->
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false"> </el-button>
        <el-button type="primary" @click="editInfo"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data () {
    return {
      queryInfo: {
        query: '',
        // 
        pagenum: 1,
        pagesize: 10
      },
      userlist: [],
      total: 0,
      // 
      modifyStatusVisible: false,
      // 
      stuInfo: {
        name: '',
        status: ''
      },
      // 
      statusList: [
        {
          id: 1,
          status: ''
        },
        {
          id: 2,
          status: ''
        }
      ],
      selectedStatusId: '',
      declarationInfoVisible: false,
      declarationInfo: [],
      ptpInfo: {},
      editDialogVisible: false,
      editFormRules: {
        motto: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      editForm: {},
      value: false
    }
  },
  created () {
    this.getParticipantsList()
  },
  methods: {
    async getParticipantsList (pgnum) {
      if (pgnum) this.queryInfo.pagenum = pgnum
      if (!this.value) {
        const { data: res } = await this.$http.get('/admin/participants', {
          params: this.queryInfo
        })
        if (res.data.meta.status !== '200') return this.$message.error(res.data.meta.msg)
        this.userlist = res.data.participantsList
        this.total = res.data.total
      } else {
        const { data: res } = await this.$http.get('/admin/newparticipants', {
          params: this.queryInfo
        })
        if (res.data.meta.status !== '200') return this.$message.error(res.data.meta.msg)
        this.userlist = res.data.participantsList
        this.total = res.data.total
      }
    },
    //  pagesize 
    handleSizeChange (newSize) {
      // console.log(newSize)
      this.queryInfo.pagesize = newSize
      this.getParticipantsList()
    },
    // 
    handleCurrentChange (newPage) {
      this.queryInfo.pagenum = newPage
      this.getParticipantsList()
    },
    setStatus (ptpInfo) {
      this.ptpInfo = ptpInfo
      this.modifyStatusVisible = true
    },
    modifyStatusClosed () {
      this.selectedStatusId = ''
      this.ptpInfo = {}
    },
    async handleStatusChange () {
      if (!this.selectedStatusId && this.selectedStatusId !== 0) {
        return this.$message.error('')
      }
      const { data: res } = await this.$http.put(`/admin/participants/${this.ptpInfo.stuId}/verify`, {
        newStatus: this.selectedStatusId
      })
      if (res.data.meta.status !== '200') {
        return this.$message.error(res.data.meta.msg)
      } else if (res.data.meta.status === '200') {
      } else {
        return this.$message.error('')
      }
      this.getParticipantsList()
      this.modifyStatusVisible = false
    },
    async setDeclarationInfo (ptpInfo) {
      const { data: res } = await this.$http.get(`/admin/participants/${ptpInfo.stuId}/declaration`)
      if (res.data.meta.status !== '200') {
        return this.$message.error('')
      }
      this.declarationInfo = res.data.declarationInfo
      this.declarationInfoVisible = true
    },
    declarationInfoClosed () {
      this.declarationInfo = {}
    },
    editDialogClosed () {
      this.editForm = {}
    },
    async editInfo () {
      const { data: res } = await this.$http.post(`/admin/participants/${this.editForm.stuId}/edit`, this.editForm)
      if (res.data.meta.status !== '200') {
        return this.$message.error('')
      }
      this.$message.success('')
      this.getParticipantsList()
      this.editDialogVisible = false
    },
    async setEditInfo (ptpInfo) {
      const { data: res } = await this.$http.get(`/admin/participants/${ptpInfo.stuId}/edit`)
      if (res.data.meta.status !== '200') {
        return this.$message.error('')
      }
      this.editForm = res.data.declarationInfo[0]
      this.editDialogVisible = true
    },
    async participantsChange () {
      this.getParticipantsList()
    }
  }
}
</script>

<style lang="less" scoped>
.isnew {
  margin-top: 10px;
}
</style>
